.ilustraciones {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: visible;
}
.ilustraciones .mobile-illustration {
  display: block;
  position: absolute;
  width: 24rem;
  margin-top: -30.2rem;
}
@media (min-width: 769px) {
  .ilustraciones .mobile-illustration {
    display: none;
  }
}
.ilustraciones .desktop-illustration,
.ilustraciones .desktop-illustration__box {
  display: none;
}
@media (min-width: 769px) {
  .ilustraciones .desktop-illustration,
.ilustraciones .desktop-illustration__box {
    display: block;
    width: 40rem;
    position: absolute;
    margin-left: -18rem;
  }
}
@media (min-width: 769px) {
  .ilustraciones .desktop-illustration .mobile-illustration,
.ilustraciones .desktop-illustration__box .mobile-illustration {
    display: none;
  }
}
.ilustraciones .desktop-illustration {
  overflow: hidden;
}
.ilustraciones .desktop-illustration__box {
  width: 12rem;
  margin-top: 8rem;
  margin-right: 22rem;
  transition: all 0.3s ease;
}
.ilustraciones .desktop-illustration__box.active {
  transition: all 0.3s ease;
  width: 16rem;
  margin-right: 30rem;
}

.questions {
  border-bottom: 1px solid hsl(240deg, 5%, 91%);
}
.questions__title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}
.questions__title.active {
  padding-bottom: 0;
}
.questions__title.active h2 {
  font-weight: 700;
  font-size: 1.38rem;
}
.questions__title:hover {
  cursor: pointer;
}
.questions__title:hover h2 {
  color: hsl(14deg, 88%, 65%);
}
.questions__title h2 {
  color: hsl(238deg, 29%, 16%);
  font-weight: 400;
  font-size: 1.2rem;
  margin-right: 4rem;
}
.questions__title img {
  margin-top: 0.5rem;
  width: 1rem;
  height: 1rem;
}
.questions__info {
  display: none;
}
.questions__info.show {
  display: flex;
}
.questions__info.show p {
  padding: 1rem 6rem 1rem 1rem;
  font-size: 1.2rem;
  color: hsl(240deg, 6%, 50%);
}